<template>

  <section class="categoryList">
    <div class="page-header">
      <h3 class="page-title" style="font-weight: bold; font-size: 20px;">
        Users
      </h3>
<!--      <nav aria-label="breadcrumb">-->
<!--        <ol class="breadcrumb">-->
<!--          <li class="breadcrumb-item"><a href="javascript:void(0);">دسته بندی جدید</a></li>-->
<!--        </ol>-->
<!--      </nav>-->
    </div>
    <div class="row">

      <div class="col-lg-12 grid-margin stretch-card">
        <div class="card">
          <div class="card-body text-center pt-0 mt-0">

            <div class="d-flex flex-row justify-content-between align-items-center pt-0 mt-3">
              <h4 class="card-title pt-3 ">All users</h4>
            </div>

            <div v-if="loading == true" class="text-secondary mt-3 mb-3" style="font-size: 14px;">
              <span
                  v-if="loading"
                  class="spinner-border spinner-border-sm mb-1 mt-1"
                  role="status"
                  aria-hidden="true"
              ></span>
            </div>
            <div v-else>
              <div v-if="items.length == 0" class="text-secondary mt-3 mb-3" style="font-size: 14px;">No Item</div>
              <table v-else class="table table-hover text-left mt-3">
                <thead>
                <tr>
<!--                  <th scope="col">Id</th>-->
                  <th scope="col">Name</th>
                  <th scope="col">Email</th>
                  <th scope="col">Last login</th>

                </tr>
                </thead>
                <tbody>


                <tr v-for="(item, index) in items" style="cursor: pointer;"
                    :key="item.id" @click="itemClick($event,index)">
<!--                  <td>{{ item.id }}</td>-->
                  <td>{{ item.name }}</td>
                  <td>{{ item.email }}</td>
                  <td>{{ timestampToDate(item.lastLogin) }}</td>
                </tr>

                </tbody>
              </table>
            </div>



          </div>
        </div>
      </div>

    </div>
  </section>

</template>

<script>
import moment from 'moment'
import api from '../../const/api'

export default {
  name: 'categoryList',
  data () {
    return {
      loading: false,
      errored: false,
      errorMessage: "",
      success: false,
      items: [],
    }
  },
  mounted() {
    this.getUsers()
  },
  methods: {
    timestampToDate: function (timestamp) {
      let date = new Date(timestamp * 1000)
      return moment(date).format("YYYY-MM-DD hh:mm")
    },
    itemClick(e,index) {
      if (e.target.classList.length == 0) {

        var row = this.items[index]
        row.id
        this.$router.push({name:'records-list',params: {
            id: row.id,
            name: row.name
          }})
      }
    },
    getUsers: function (){

      this.loading = true;
      this.axios
          .post(api.baseURL + '/panel/users.php', {
            headers: {
              Authorization: "Bearer " + localStorage.token
            }
          },)
          .then(response => {

            // eslint-disable-next-line no-console
            console.log(response)
            this.loading = false
            if (response.status == 200) {
              this.items = response.data.list
            } else {
              this.errored = true
              this.errorMessage = response.data.message
            }

          })
          .catch(error => {
            // eslint-disable-next-line no-console
            console.log(error)
            this.loading = false
            this.errored = true
            this.errorMessage = 'Please try again'
          });

    },
  }
}
</script>